// @flow

import React, { Component } from 'react'
import { Table } from 'reactstrap'
import { fanConversion } from '../../data'

export class ConvertTable extends Component<Props> {
  render() {
    const headers = ['Fan', 'Points', 'On Discard', 'On Draw']

    return (
      <Table size="sm" responsive striped bordered>
        <thead>
          <tr>
            {headers.map((item, idx) => (
              <th key={idx}>{item}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {fanConversion.map((item, idx) => (
            <tr key={idx}>
              <td>{item.fan}</td>
              <td>{item.points}</td>
              <td>{item.discard}</td>
              <td>{item.draw}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    )
  }
}
