// @flow

import React, { Component } from 'react'
import { ScoringTable } from '../ScoringTable'

import './style.scss'

type Props = {
  title: string,
  data?: Object[],
  table?: Component
}

export class Section extends Component<Props> {
  handleRender = () => {}

  render() {
    const { table: Table, title, data } = this.props

    return (
      <div className="Section">
        <h2>{title}</h2>
        {Table ? <Table /> : <ScoringTable data={data} />}
      </div>
    )
  }
}
