// @flow

import React, { Component } from 'react'
import { Table } from 'reactstrap'

type Props = {
  data: Object[]
}

export class ScoringTable extends Component<Props> {
  render() {
    const { data } = this.props
    const headers = ['Fan', 'Hand', 'Description']

    return (
      <Table size="sm" responsive striped bordered>
        <thead>
          <tr>
            {headers.map((item, idx) => (
              <th key={idx}>{item}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.map((item, idx) => (
            <tr key={idx}>
              <td>{item.fan}</td>
              <td>{item.hand}</td>
              <td>{item.description}</td>
            </tr>
          ))}
        </tbody>
      </Table>
    )
  }
}
